import { DefaultProps } from './Props';
import {
    validateOptions,
} from './Utils'

class graphiteTabs {
    constructor(selector, config) {
        validateOptions(config || {}, DefaultProps);

        this.tabButtons = document.querySelectorAll(selector);
    
        Props = {
            activeClass: config.activeClass || DefaultProps.activeClass,
            tabPanes: config.tabPanes || DefaultProps.tabPanes
        };

        this.tabPanes = document.querySelectorAll(DefaultProps.tabPanes);
    }

    init() {
        if (!this.tabButtons) {
            throw Error('Cannot find tab buttons');
        }
    
        if (this.tabButtons) {
            this.tabButtons.forEach((button) => {
                button.addEventListener('click', (event) => {
                    event.preventDefault();
                    this.hideTabs();
                    this.showTab(button);
                });
            });
        }
    }

    showTab(button) {
        this.button = button;
        this.targetTabPane = document.querySelector(`#${this.button.getAttribute('aria-controls')}`)

        if (!this.button) {
            throw Error('Cannot find tab button');
        }

        if (!targetTabPane) {
            throw Error('Cannot find target tab pane from tab button aria-controls');
        };

        this.targetTabPane.classList.add(DefaultProps.activeClass);
        this.button.setAttribute('aria-selected', 'true');
    }

    hideTabs() {
        // Hide any opened tabs and toggle aria state of button
        for (let i = 0; i < this.tabButtons.length; i += 1) {
            this.tabButtons[i].setAttribute('aria-selected', 'false');
            this.tabPanes[i].classList.remove(DefaultProps.activeClass);
        }
    }

}


export default graphiteTabs;
